<template>
  <div>
    <loading :active.sync="isLoading"></loading>
    <div class="mt-3 text-right">
      <button type="button"
      class="btn btn-primary" @click="openModal(true)"></button>
    </div>
    <table class="table mt-3">
      <thead>
        <tr>
          <th scope="col" width="10%"></th>
          <th scope="col"></th>
          <th scope="col" width="10%"></th>
          <th scope="col" width="10%"></th>
          <th scope="col" width="10%"></th>
          <th scope="col" width="10%"></th>
          <th scope="col" width="10%"></th>
        </tr>
      </thead>
      <tbody>
        <tr v-for="item in products" :key="item.id">
          <td>{{ item.category }}</td>
          <td>{{ item.title }}</td>
          <td class="text-right">{{ item.price | currency }}</td>
          <td class="text-right">{{ item.origin_price | currency }}</td>
          <td>
            <span v-if="item.is_enabled"></span>
            <span v-else></span>
          </td>
          <td>
            <button class="btn btn-outline-primary btn-sm"
            @click="openModal(false,item)"></button>
          </td>
          <td>
            <button class="btn btn-outline-secondary btn-sm"
            @click="deleteModal(item)"></button>
          </td>
        </tr>
      </tbody>
    </table>
    <!-- pagespaginationdata:pagination -->
    <Pagination :pages="pagination" @getPage="getProduct" />
    <!-- Modal -->
<div class="modal fade" id="productModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog modal-lg" role="document">
    <div class="modal-content border-0">
      <div class="modal-header bg-dark text-white">
        <h5 class="modal-title" id="exampleModalLabel">
          <span></span>
        </h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
        <div class="row">
          <div class="col-sm-4">
            <div class="form-group">
              <label for="image"></label>
              <input type="text" class="form-control" id="image"
                placeholder="" v-model="tempProduct.imageUrl">
            </div>
            <div class="form-group">
              <label for="customFile"> 
                <i class="fas fa-spinner fa-spin" v-if="status.fileUploading"></i>
              </label>
              <input type="file" id="customFile" class="form-control"
                ref="files" @change="uploadFile">
            </div>
            <img img="
            https://images.unsplash.com/photo-1483985988355-763728e1935b?ixlib=rb-0.3.5&
            ixid=eyJhcHBfaWQiOjEyMDd9&s=828346ed697837ce808cae68d3ddc3cf&auto=format
            &fit=crop&w=1350&q=80"
              class="img-fluid" :src="tempProduct.imageUrl" alt="">
          </div>
          <div class="col-sm-8">
            <div class="form-group">
              <label for="title"></label>
              <input type="text" class="form-control" id="title"
                placeholder="" v-model="tempProduct.title">
            </div>
            <div class="form-group">
              <label for="category"></label>
              <input type="text" class="form-control" id="category"
                placeholder="" v-model="tempProduct.category">
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
                <label for="num"></label>
                <input type="number" class="form-control" id="num"
                  placeholder="" v-model="tempProduct.num">
              </div>
              <div class="form-group col-md-6">
                <label for="price"></label>
                <input type="unit" class="form-control" id="unit"
                  placeholder="" v-model="tempProduct.unit">
              </div>
            </div>
            <div class="form-row">
              <div class="form-group col-md-6">
              <label for="origin_price"></label>
                <input type="number" class="form-control" id="origin_price"
                  placeholder="" v-model="tempProduct.origin_price">
              </div>
              <div class="form-group col-md-6">
                <label for="price"></label>
                <input type="number" class="form-control" id="price"
                  placeholder="" v-model="tempProduct.price">
              </div>
            </div>
            <hr>
            <div class="form-group">
              <label for="description"></label>
              <textarea type="text" class="form-control" id="description"
                placeholder="" v-model="tempProduct.description"></textarea>
            </div>
            <div class="form-group">
              <label for="content"></label>
              <textarea type="text" class="form-control" id="content"
                placeholder="" v-model="tempProduct.content"></textarea>
            </div>
            <div class="form-group">
              <div class="form-check">
                <input class="form-check-input" type="checkbox"
                  id="is_enabled" v-model="tempProduct.is_enabled"
                  :true-value="1" :false-value="0">
                <label class="form-check-label" for="is_enabled">
                  
                </label>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
        <button type="button" class="btn btn-primary" @click="updateProduct"></button>
      </div>
    </div>
  </div>
</div>
    <!-- delModal -->
<div class="modal fade" id="delProductModal" tabindex="-1" role="dialog"
  aria-labelledby="exampleModalLabel" aria-hidden="true">
  <div class="modal-dialog" role="document">
    <div class="modal-content border-0">
      <div class="modal-header bg-danger text-white">
        <h5 class="modal-title" id="exampleModalLabel">
          <span></span>
        </h5>
        <button type="button" class="close" data-dismiss="modal" aria-label="Close">
          <span aria-hidden="true">&times;</span>
        </button>
      </div>
      <div class="modal-body">
         <strong class="text-danger">{{ tempProduct.title }}</strong> ()
      </div>
      <div class="modal-footer">
        <button type="button" class="btn btn-outline-secondary" data-dismiss="modal"></button>
        <button type="button" class="btn btn-danger" @click="deleteProduct"
          ></button>
      </div>
    </div>
  </div>
</div>
  </div>
</template>

<script>
import $ from 'jquery';
import Pagination from '@/components/Pagination.vue';

export default {
  components: {
    Pagination,
  },
  data() {
    return {
      products: [],
      tempProduct: {},
      pagination: {},
      isNew: false,
      isLoading: false,
      status: {
        fileUploading: false,
      },
    };
  },
  methods: {
    openModal(isNew, item) {
      if (isNew) {
        this.tempProduct = {};
        this.isNew = true;
      } else {
        this.tempProduct = { ...item };
        this.isNew = false;
      }
      $('#productModal').modal('show');
    },
    deleteModal(item) {
      this.tempProduct = { ...item };
      $('#delProductModal').modal('show');
    },
    getProduct(page = 1) {
      const vm = this;
      vm.isLoading = true;
      vm.$http.get(`${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/products/?page=${page}`).then((res) => {
        vm.products = res.data.products;
        vm.pagination = res.data.pagination;
        vm.isLoading = false;
      });
    },
    updateProduct() {
      const vm = this;
      let api = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product`;
      let httpMethod = 'post';
      if (!vm.isNew) {
        api = `
        ${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`;
        httpMethod = 'put';
      }
      vm.$http[httpMethod](api, { data: vm.tempProduct }).then((res) => {
        if (res.data.success) {
          $('#productModal').modal('hide');
          vm.getProduct();
        } else {
          $('#productModal').modal('hide');
          vm.getProduct();
        }
      });
    },
    deleteProduct() {
      const vm = this;
      vm.$http.delete(`${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/product/${vm.tempProduct.id}`).then((res) => {
        if (res.data.success) {
          $('#delProductModal').modal('hide');
          vm.getProduct();
        } else {
          $('#delProductModal').modal('hide');
          vm.getProduct();
        }
      });
    },
    uploadFile() {
      const vm = this;
      const uploadedFile = vm.$refs.files.files[0];
      vm.status.fileUploading = true;
      const formdata = new FormData();
      formdata.append('file-to-upload', uploadedFile);
      const url = `${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/admin/upload`;
      vm.$http.post(url, formdata, {
        Headers: {
          'Content-type': 'multipart/form-data',
        },
      }).then((res) => {
        if (res.data.success) {
          vm.$set(vm.tempProduct, 'imageUrl', res.data.imageUrl);
        } else {
          vm.$bus.$emit('message:push', res.data.message, 'danger');
        }
        vm.status.fileUploading = false;
      });
    },
  },
  created() {
    this.getProduct();
  },
};
</script>
