<template>
<div>
  <div class="my-5 row justify-content-center">
    <form class="col-md-10" @submit.prevent="payOrder">
      <table class="table">
        <thead>
          <th></th>
          <th></th>
          <th></th>
          <th></th>
        </thead>
        <tbody>
          <tr class="cartlist" v-for="item in order.products" :key="item.id">
            <td width="40%" class="align-middle tableimage"
            :style="{backgroundImage:`url(${item.product.imageUrl})`}">
            </td>
            <td class="align-middle">
              {{ item.product.title }}
              <div class="text-success" v-if="item.coupon">
                
              </div>
            </td>
            <td class="align-middle">{{ item.qty }}{{ item.product.unit }}</td>
            <td class="align-middle">
              <del v-if="item.total!==item.final_total">{{ item.total }}</del>
              {{ item.final_total }}
              <div v-if="item.qty!==1" style="font-size:0.5rem">
                {{ item.product.price }}/{{ item.product.unit }}
              </div>
            </td>
            </tr>
        </tbody>
        <tfoot>
          <tr>
            <td colspan="3"></td>
            <td>{{ order.total }}</td>
          </tr>
        </tfoot>
      </table>
      <table class="table">
        <tbody>
          <tr>
            <th width="100">Email</th>
            <td>{{ order.user.email }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.name }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.tel }}</td>
          </tr>
          <tr>
            <th></th>
            <td>{{ order.user.address }}</td>
          </tr>
          <tr>
            <th></th>
            <td>
              <span v-if="!order.is_paid" class="text-danger"></span>
              <span v-else class="text-success"></span>
            </td>
          </tr>
        </tbody>
      </table>
      <div class="text-right" v-if="order.is_paid === false">
        <button class="btn btn-primary mb-3"></button>
      </div>
    </form>
  </div>
</div>
</template>

<script>
export default {
  data() {
    return {
      orderId: '',
      order: {
        user: {}, // object
      },
    };
  },
  methods: {
    getOrder() {
      const vm = this;
      vm.$http.get(`${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/order/${vm.orderId}`).then((res) => {
        vm.order = res.data.order;
      });
    },
    payOrder() {
      const vm = this;
      vm.$http.post(`${process.env.VUE_APP_APIPATH}/api/${process.env.VUE_APP_CUSTOMPATH}/pay/${vm.orderId}`).then(() => {
        vm.getOrder();
      });
    },
  },
  created() {
    const vm = this;
    vm.orderId = vm.$route.params.orderId;
    vm.getOrder();
  },
};
</script>

<style lang="scss" scoped>
@import"@/assets/scss/all";

.table {
  background:rgba(255, 255, 255, 0.5);
  border-radius: 1rem;

  th , td {
    border:none;
  }

  .cartlist {
    opacity:0.7;
    transition: 0.3s;

    &:hover {
      background:rgba(255, 255, 255, 0.7);
      opacity:1;
    }
  }

  .tableimage {
    background-size: cover;
    background-position: center;
    height:150px;
  }

  tfoot tr {
    background:#575757;
    color:#fff;
    font-weight: bold;

    td:first-child{
      border-radius:0 0 0 1rem;
    }

    td:last-child{
      border-radius:0 0 1rem 0;
    }
  }
}
</style>
